



























import Vue from "vue";
import SNSList from "./SNSList.vue";

export default Vue.extend({
  name: "SideMenu",
  components: {
    SNSList
  },
  props: {
    onClick: Function,
    activeContentName: String
  },
  computed: {
    setActive() {
      if (!this.activeContentName) {
        return true;
      }
      return this.activeContentName === "works";
    }
  }
});
